// MORE PHOTOS

.photos__container {
  margin: 0 auto;
  margin-bottom: 60px;
  padding: 0 20px;
  width: 100%;

  @media screen and (min-width: 480px) {
    max-width: 480px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 0 32px;
    margin-bottom: 80px;
  }

  @media screen and (min-width: 1440px) {
    max-width: 1440px;
    padding: 0 149px;
    margin-bottom: 100px;
  }
}

.photos__title {
  // @include font (null, 48px, 1.25, null, $accentTextColor);

  font-size: 24px;
  line-height: 1.25;
  margin-bottom: 16px;
  color: $accentTextColor;

  @media screen and (min-width: 768px) {
    font-size: 36px;
    line-height: 1.25;
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1440px) {
    font-size: 48px;
    line-height: 1.25;
    margin-bottom: 60px;
  }
}

.photos__list {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin: -16px;
  }
}

.photos__item:not(:last-child) {
  margin-bottom: 16px;
}

.photos__item {
  @media screen and (min-width: 768px) {
    margin: 16px;
  }
}

